<template>
    <div class="wrapper" :style="{ '--value': parseInt(progressValue)/100 }">
        <div class="line">
            
        </div>

        <div class="wrapper wrapper__inner">
            <div class="line line__inner">
                
            </div>
        </div>

        <span class="indiсator">{{ parseInt(progressValue) + "%" }}</span>
    </div>
</template>

<script>
export default {
    props: {
        progressValue: {
            type: [String, Number],
            required: true,
        }
    },

    data() {
        return {
            // progressValue: 0.35,
        };
    },
};
</script>

<style lang="scss" scoped>
.wrapper {
    --wrapPadding: 22px;
    --wrapPaddingTop: calc(var(--wrapPadding) + 20px);
    --bgColor: rgb(27, 66, 64);
    --lineColor: rgb(101, 102, 112);
    --lineColorFill: rgb(224, 221, 14);
    --lineWidth: 230px;
    --lineHeight: 8px;
    --zIndex: 1;
    --cursorTransition: 1s;

    // max-width: 100%;
    padding: var(--wrapPadding);
    padding-top: var(--wrapPaddingTop);
    background: linear-gradient(0deg, var(--bgColor) 0%, var(--bgColor) 40%, rgba(0, 0, 0, 0) 100%);
    border-radius: 18px 18px 12px 12px;
    position: relative;


    .line {
        width: var(--lineWidth);
        height: var(--lineHeight);
        border-radius: 8px;        
        background-color: var(--lineColor);
        position: relative;
        z-index: 5; 
        outline: 1px solid var(--lineColorFill);
        outline-style: dotted;
        outline-offset: -1px;
    }

    &.wrapper__inner {
        position: absolute;
        left: 0;
        top: 0;
        width: calc(var(--lineWidth) * var(--value));
        outline-offset: -2px;
        overflow: hidden;
        background: none;
        transition: var(--cursorTransition);
        padding-right: 0;
        .line__inner {
            --lineColor: var(--lineColorFill);
            --lineWidthInner: calc(var(--lineWidth));
            width: var(--lineWidthInner);
            box-shadow: 0 0 12px 1px var(--lineColor);
 
        }
    }
    .indiсator {
        --indWidth: 50px;
        --indHeight: 28px;
        --indPadding: 4px;
        --indBorder: 2px;
        position: absolute;
        width: var(--indWidth);
        height: var(--indHeight);
        box-sizing: border-box;
        padding: var(--indPadding);
        display: flex;
        line-height: 1em;
        border: var(--indBorder) solid var(--bgColor);
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        background: rgb(255, 255, 255);
        font-size: 16px;
        font-weight: bold;
        color: rgb(0, 0, 0);
        top: 0px;
        left: calc(var(--lineWidth) * var(--value) + var(--wrapPadding) - var(--indWidth) / 2);
        transition: var(--cursorTransition);

        &::before {
            --indCursor: 2px;
            position: absolute;
            content: "";
            width: var(--indCursor);
            height: calc(var(--wrapPaddingTop) - var(--indHeight) + var(--indBorder) + var(--lineHeight) / 2);
            top: calc(100% - 0px);
            left: calc(50% - var(--indCursor));
            background: rgb(255, 255, 255);
            background: linear-gradient(0deg, var(--lineColorFill) 0%, rgb(255, 255, 255) 100%);
        }
    }
}
</style>
