<template>
    <div ref="root" class="loaderPiano-wrapper">
        <!-- <button v-if="true" @click="addAnimation"></button> -->
        <div class="loaderPiano" ref="loaderPiano">
            <div
                v-for="i in dotsCount"
                :class="{ loaderPiano__dot: true, animated: true, ['dot' + i]: true }"
                :style="{ '--num': i }"
                :data-num="i"
                :key="i"
                :ref="setItemRef"
            ></div>
            <!-- <div class="loaderPiano__dot dot1" style="--num: 1" ref="1"></div>
            <div class="loaderPiano__dot dot2" style="--num: 2" ref="2"></div>
            <div class="loaderPiano__dot dot3" style="--num: 3" ref="3"></div>
            <div class="loaderPiano__dot dot4" style="--num: 4" ref="4"></div>
            <div class="loaderPiano__dot dot5" style="--num: 5" ref="5"></div> -->
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dotsCount: 5,
            itemRefs: [],
            animationForward: true,
            rootWidth: undefined,
            rootHeight: undefined,
        };
    },
    methods: {
        setItemRef(el) {
            if (el) {
                this.itemRefs.push(el);
            }
        },
        directionSwitcher(event) {
            if (event.target === this.lastItem) {
                this.removeAnimation();
                this.animationForward = !this.animationForward;
            }
        },
        addAnimation() {
            this.itemRefs.forEach((el) => {
                el.classList.add("animated");
            });
        },
        removeAnimation() {
            this.itemRefs.forEach((el) => {
                el.classList.remove("animated");
            });
        },
        detectSize() {
            this.rootWidth = this.$refs.root.offsetWidth;
            this.rootHeight = this.$refs.root.offsetHeight;
        },
    },
    watch: {
        animationForward(newValue) {
            if (newValue) {
                this.$refs.loaderPiano.classList.remove("revers");
            } else {
                this.$refs.loaderPiano.classList.add("revers");
            }
            setTimeout(this.addAnimation, 100);
        },
        rootHeight(newHeight) {
            this.$refs.root.style.setProperty("--calcHeight", newHeight + "px");
        },
        rootWidth(newWidth) {
            this.$refs.root.style.setProperty("--calcWidth", newWidth + "px");
        },
    },
    computed: {
        lastItem() {
            if (this.animationForward) {
                return this.itemRefs[this.itemRefs.length - 1];
            } else return this.itemRefs[0];
        },
    },
    mounted() {
        this.itemRefs[0].onanimationend = this.itemRefs[this.itemRefs.length - 1].onanimationend = this.directionSwitcher;
        this.detectSize();
        this.$refs.root.style.setProperty("--dotsQuantity", this.dotsCount);
    },
    updated() {
        this.detectSize();
    },
};
</script>

<style lang="scss" scoped>
.loaderPiano-wrapper {
    --size0: 70px;
    --bgDot: rgb(112, 141, 184);
    --dotBgRGB: 138, 164, 204;
    --dotsQuantity: 5;
    --animDel: calc((var(--num) - 1) * 0.18s);
    --animDelRev: calc((var(--dotsQuantity) - var(--num)) * 0.18s);
    --size: calc(var(--size0) * 2);

    height: 150px;
    width: 200px;
    max-height: 100%;
    max-width: 100%;

    .loaderPiano {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        gap: 2.6%;

        .loaderPiano__dot {
            --dotSize: calc(100% / var(--dotsQuantity) * 0.85);
            width: var(--dotSize);
            padding-top: var(--dotSize);
            flex: 0 0 auto;
            border-radius: 50%;
            outline: 1px solid rgb(7, 72, 156);
            outline-offset: -1px;
            background: rgba(var(--dotBgRGB), 0.7);
            box-shadow: 0 2px 3px rgba(0, 0, 0, 0.25);

            &.animated {
                animation: jump 0.5s 1;
                animation-timing-function: ease-in-out;
                animation-delay: calc((var(--num) - 1) * 0.18s);

                @keyframes jump {
                    50% {
                        transform: translateY(calc(var(--calcWidth) / var(--dotsQuantity) * 0.85 - var(--calcHeight)));
                        box-shadow: 0 calc(var(--calcHeight)) calc(var(--calcWidth) * 0.18 * 0.7) calc(var(--calcWidth) * 0.18 * 0.2)
                            rgba(0, 0, 0, 0.16);
                    }
                }
            }
        }
        &.revers {
            .loaderPiano__dot {
                &.animated {
                    animation-delay: calc((var(--dotsQuantity) - var(--num)) * 0.18s);
                }
            }
        }
    }
}
</style>
