<template>
    <div class="container">
        <div class="controlSide">
            <input type="text" v-model="inputValue" @keyup.enter="requestDataUser" />
            <button class="btn-request" @click="requestDataUser">requestDataUser</button>
            <button class="btn-request" @click="requestDataUserServer">requestDataUserServer</button>
        </div>
        <div class="resultSide">
            <MyRenderedTable v-if="!dataLoading" :dataObj="dataObj"></MyRenderedTable>
            <MyLoader v-else></MyLoader>
        </div>
    </div>
</template>

<script>
import MyRenderedTable from "@/components/MyRenderedTable.vue";
import MyLoader from "@/components/MyLoader.vue";

export default {
    name: "MyDataToTableDemo",
    components: { MyRenderedTable, MyLoader },
    data() {
        return {
            inputValue: "users/1",
            dataObj: null,
            dataLoading: false,
        };
    },
    methods: {
        requestDataUser() {
            this.dataLoading = true;
            const url = "https://jsonplaceholder.typicode.com/" + this.inputValue;
            fetch(url)
                .then((response) => response.json())
                // .then(json => {console.log(json); return json})
                .then((json) => {
                    console.log(json);
                    return json;
                })
                .then((json) => {
                    this.dataObj = json;
                    console.log(this.dataObj);
                    this.dataLoading = false;
                });
        },
        requestDataUserServer() {
            this.dataLoading = true;
            const url = "http://localhost:3000/" + this.inputValue;
            fetch(url)
                .then((response) => response.json())
                // .then(json => {console.log(json); return json})
                .then((json) => {
                    console.log(json);
                    return json;
                })
                .then((json) => {
                    this.dataObj = json;
                    console.log(this.dataObj);
                    // this.dataLoading = false;
                })
                .finally(()=>{this.dataLoading = false;})
        },
    },
    
    // computed:{
    //     dataObj(){

    //     }
    // }
};
</script>

<style lang="scss" scoped>
.container {
    margin-top: 25px;
    display: flex;
    align-items: stretch;
    border: 5px solid rgba(141, 66, 66, 0.315);
    padding: 3px;
    .controlSide {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 10px;
        padding: 5px;

        background: #6969696e;
    }
    .resultSide {
        padding: 5px;
    }
}
</style>
