<template>
    <img class="officialLogo" alt="Vue logo" src="./assets/logo.png" />
    <div class="mainContent">
        <div class="mainContent__side controlSide">
            <h3 class="controlSide__header">change the value</h3>
            <input class="input-range" type="range" v-model="progressValue" />
            <input class="input-number" type="number" v-model="progressValue" />
            <button class="btn__dataQuery" @click="clickQuery_1">data query from JSON</button>
        </div>
        <div class="mainContent__side representSide">
            <MyProgressBar_1 :progressValue="progressValue" :dataOnLoad="dataOnLoad"></MyProgressBar_1>
            <MyProgressBar_2 :progressValue="progressValue"></MyProgressBar_2>
        </div>
    </div>
    <div class="demoBox">
        <h3 class="demoBox__header">MyLoader</h3>
        <div class="demoBox__container"><MyLoader v-if="true" :style="{ '--size': loaderSize }"></MyLoader></div>
    </div>
    <div class="demoBox">
        <h3 class="demoBox__header">MyLoader<wbr />Piano</h3>
        <div class="demoBox__container"><MyLoaderPiano :style="{ '--size': '150px' }"></MyLoaderPiano></div>
    </div>
    <div class="demoBox">
        <h3 class="demoBox__header">MyLoader<wbr />Piano2</h3>
        <div class="demoBox__container" :style="{ width: containerWidth, height: containerHeight }">
            <MyLoaderPiano2 :style="{ width: containerWidth, height: containerHeight }"></MyLoaderPiano2>
            <label class="rangeLabel-width">
                <input class="input-range" type="range" min="30" max="200" v-model="contWidthValue" />
                <strong> W =&nbsp;{{ containerWidth }}</strong>
            </label>
            <label class="rangeLabel-height">
                <strong> H =&nbsp;{{ containerHeight }}</strong>
                <input class="input-range" type="range" min="20" max="140" v-model="contHeightValue" />
            </label>
        </div>
    </div>
    <MyDataToTableDemo class="demoBox"></MyDataToTableDemo>

    
</template>

<script>
import MyProgressBar_1 from "@/components/MyProgressBar_1.vue";
import MyProgressBar_2 from "@/components/MyProgressBar_2.vue";
import MyLoader from "@/components/MyLoader.vue";
import MyLoaderPiano from "@/components/MyLoaderPiano.vue";
import MyLoaderPiano2 from "@/components/MyLoaderPiano2.vue";
import MyDataToTableDemo from "@/components/MyDataToTableDemo.vue";
import axios from "axios";

export default {
    name: "App",
    components: { MyProgressBar_1, MyProgressBar_2, MyLoader, MyLoaderPiano, MyLoaderPiano2, MyDataToTableDemo },
    data() {
        return {
            progressValue: "75",
            dataOnLoad: false,
            contWidthValue: 140,
            contHeightValue: 90,
        };
    },
    methods: {
        onInput(event) {
            console.log(event.target.value);
        },
        async clickQuery_1() {
            console.log("clickQuery1");
            try {
                this.dataOnLoad = true;
                const response = await axios.get("data/progressData.json");
                let data = response.data;
                let value = Math.round((data.done / data.total) * 100);
                this.progressValue = value.toString();
            } catch (e) {
                console.log("clickQuery ERROR", e);
            }
        },
    },
    computed: {
        loaderSize() {
            return parseInt(this.progressValue) + "px";
        },
        containerWidth() {
            return Math.round(this.contWidthValue) + "px";
        },
        containerHeight() {
            return Math.round(this.contHeightValue) + "px";
        },
    },
    watch: {
        progressValue(newVal) {
            if (parseInt(newVal) > 100) {
                this.progressValue = "100";
            }
            if (parseInt(newVal) < 0 || !parseInt(newVal)) {
                this.progressValue = "0";
            }
        },
    },
    mounted() {},
};
</script>

