<template>
    <div class="loaderPiano-wrapper">
        <div class="loaderPiano" ref="loaderPiano">
            <div
                v-for="i in dotsCount"
                :class="{ loaderPiano__dot: true, animated: true, ['dot' + i]: true }"
                :style="{ '--num': i }"
                :data-num="i"
                :key="i"
                :ref="setItemRef"
            ></div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dotsCount: 5,
            itemRefs: [],
            animationForward: true,
        };
    },
    methods: {
        setItemRef(el) {
            if (el) {
                this.itemRefs.push(el);
            }
        },
        directionSwitcher(event) {
            if (event.target === this.lastItem) {
                this.removeAnimation();
                this.animationForward = !this.animationForward;
            }
        },
        addAnimation() {
            this.itemRefs.forEach((el) => {
                el.classList.add("animated");
            });
        },
        removeAnimation() {
            this.itemRefs.forEach((el) => {
                el.classList.remove("animated");
            });
        },
    },
    watch: {
        animationForward(newValue) {
            let k;
            for (let i = 0; i < this.itemRefs.length; i++) {
                if (newValue) {
                    k = i + 1;
                } else {
                    k = this.itemRefs.length - i;
                }
                this.itemRefs[i].style.setProperty("--num", k);
            }
            setTimeout(this.addAnimation, 200);
        },
    },
    computed: {
        lastItem() {
            if (this.animationForward) {
                return this.itemRefs[this.itemRefs.length - 1];
            } else return this.itemRefs[0];
        },
    },
    mounted() {
        this.itemRefs[0].onanimationend = this.itemRefs[this.itemRefs.length - 1].onanimationend = this.directionSwitcher;
        this.lastItem.onanimationend = this.directionSwitcher;
    },
};
</script>

<style lang="scss" scoped>
.loaderPiano-wrapper {
    --size: 150px;
    --dotBgRGB: 22, 95, 206;

    .loaderPiano {
        height: calc(var(--size) * 0.4);
        padding: calc(var(--size) * 0.03);
        display: flex;
        align-items: flex-end;
        gap: calc(var(--size) * 0.02);

        .loaderPiano__dot {
            width: calc(var(--size) * 0.12);
            height: calc(var(--size) * 0.12);
            border-radius: 50%;
            border: 1px solid rgb(7, 72, 156);
            background: rgba(var(--dotBgRGB), 0.267);
            --shadow: 0 3px 3px rgba(0, 0, 0, 0.25);
            box-shadow: var(--shadow);

            &.animated {
                animation: jump 0.5s 1;
                animation-timing-function: ease-in-out;
                animation-delay: calc((var(--num) - 1) * 0.18s);
            }

            @keyframes jump {
                50% {
                    transform: translateY(calc(0px - var(--size) * 0.3));
                    box-shadow: 0 calc(var(--size) * 0.45) 16px 5px rgba(0, 0, 0, 0.18);
                }
            }
        }
    }
}
</style>
