<template>
    <div class="wrapper" :style="{ '--value': parseInt(progressValue) / 100 }">
        <div class="line">
            <div class="circle circle-25"><div class="circle__innerCircle"></div></div>
            <div class="circle circle-50"><div class="circle__innerCircle"></div></div>
            <div class="circle circle-75"><div class="circle__innerCircle"></div></div>
            <div class="circle circle-100"><div class="circle__innerCircle"></div></div>
        </div>

        <div class="wrapper wrapper__inner">
            <div class="line line__inner">
                <div class="circle circle-25"><div class="circle__innerCircle"></div></div>
                <div class="circle circle-50"><div class="circle__innerCircle"></div></div>
                <div class="circle circle-75"><div class="circle__innerCircle"></div></div>
                <div class="circle circle-100"><div class="circle__innerCircle"></div></div>
            </div>
        </div>

        <span class="indiсator">
            <MyLoader
                class="loader"
                v-if="false"
                :style="{ '--size': '', '--b': 'calc(var(--size) * 0.30)', '--n': '7', '--g': '20deg', border: 'none', padding: '0' }"
            ></MyLoader>
            <!-- <MyLoaderPiano2 :style="{ 'max-height': '120%'}"></MyLoaderPiano2> -->
            <!-- <MyLoaderPiano2></MyLoaderPiano2> -->
            <MyLoaderPiano2 :style="{ 'max-height': '120%', 'max-width': '90%'}"></MyLoaderPiano2>
            {{ !dataOnLoad && false ? parseInt(progressValue) + "%" : "" }}
        </span>
    </div>
</template>

<script>
import MyLoader from "@/components/MyLoader.vue";
import MyLoaderPiano2 from "@/components/MyLoaderPiano2.vue";

export default {
    components: { MyLoader, MyLoaderPiano2 },
    props: {
        progressValue: {
            type: [String, Number],
            required: true,
        },
        dataOnLoad: {
            type: Boolean,
            required: false,
        },
    },

    data() {
        return {
            // progressValue: 0.35,
        };
    },
};
</script>

<style lang="scss" scoped>
.wrapper {
    --wrapPadding: 22px;
    --wrapPaddingTop: calc(var(--wrapPadding) + 20px);
    --bgColor: rgb(27, 66, 64);
    --lineColor: rgb(101, 102, 112);
    --lineColorFill: rgb(224, 221, 14);
    --lineWidth: 230px;
    --lineHeight: 8px;
    --circleDiameter: 16px;
    --circleDiameterBig: calc(var(--circleDiameter) + 4px);
    --zIndex: 1;
    --cursorTransition: 1s;

    padding: var(--wrapPadding);
    padding-top: var(--wrapPaddingTop);
    background: linear-gradient(0deg, var(--bgColor) 0%, var(--bgColor) 40%, rgba(0, 0, 0, 0) 100%);
    border-radius: 18px 18px 12px 12px;
    position: relative;

    .line {
        width: var(--lineWidth);
        height: var(--lineHeight);
        border-radius: 8px;
        background-color: var(--lineColor);
        position: relative;
        z-index: 5;

        .circle {
            position: absolute;
            width: var(--circleDiameter);
            height: var(--circleDiameter);
            border-radius: 50%;
            background: var(--lineColor);
            // background: rgb(148, 247, 124);
            display: flex;
            justify-content: center;
            align-items: center;

            top: calc(var(--lineHeight) / 2 - var(--circleDiameter) / 2);

            &__innerCircle {
                width: calc(100% - 6px);
                height: calc(100% - 6px);
                background: var(--bgColor);
                border-radius: 50%;
            }

            &-25 {
                left: calc(25% - var(--circleDiameter));
            }
            &-50 {
                left: calc(50% - var(--circleDiameter));
            }
            &-75 {
                left: calc(75% - var(--circleDiameter));
            }
            &-100 {
                --circleDiameter: var(--circleDiameterBig);
                left: calc(100% - var(--circleDiameter));
            }
        }
    }

    &.wrapper__inner {
        position: absolute;
        left: 0;
        top: 0;
        // opacity: 1;
        width: calc(var(--lineWidth) * var(--value));
        // outline: 2px solid red;
        outline-offset: -2px;
        overflow: hidden;
        background: none;
        transition: var(--cursorTransition);
        padding-right: 0;
        .line__inner {
            --lineColor: var(--lineColorFill);
            --lineWidthInner: calc(var(--lineWidth));
            width: var(--lineWidthInner);

            box-shadow: 0 0 12px 1px var(--lineColor);

            .circle {
                box-shadow: 0 0 10px 1px var(--lineColor);

                &__innerCircle {
                    box-shadow: inset 0 0 6px 0px var(--lineColor);
                    background: rgb(201, 44, 140);
                }
            }
        }
    }
    .indiсator {
        --indWidth: 50px;
        --indHeight: 28px;
        --indPadding: 4px;
        --indBorder: 2px;
        position: absolute;
        width: var(--indWidth);
        height: var(--indHeight);
        box-sizing: border-box;
        padding: var(--indPadding);
        display: flex;
        line-height: 1em;
        border: var(--indBorder) solid var(--bgColor);
        justify-content: center;
        align-items: center;
        border-radius: 10px;
        background: rgb(255, 255, 255);
        font-size: 16px;
        font-weight: bold;
        color: rgb(0, 0, 0);
        top: 0px;
        left: calc(var(--lineWidth) * var(--value) + var(--wrapPadding) - var(--indWidth) / 2);
        transition: var(--cursorTransition);
        .loader {
            --size: calc(var(--indHeight) * 0.8);
        }
        &::before {
            --indCursor: 2px;
            position: absolute;
            content: "";
            width: var(--indCursor);
            // height: calc(var(--wrapPaddingTop) - var(--indHeight) - var(--indBorder));
            height: calc(var(--wrapPaddingTop) - var(--indHeight) + var(--indBorder) + var(--lineHeight) / 2);
            top: calc(100% - 0px);
            // top: calc(0px - var(--indBorder) - 1px);
            left: calc(50% - var(--indCursor));
            background: rgb(255, 255, 255);
            background: linear-gradient(0deg, var(--lineColorFill) 0%, rgb(255, 255, 255) 100%);
        }
    }
}
</style>
